<template>
  <div class="hello">
    <v-data-table
      v-if="loaded"
      :headers="headers"
      :items="data"
      :dark="true"
      hide-actions>
      <template
        slot="items"
        slot-scope="props">
        <td>{{props.item.Proximity}}</td>
        <td>{{props.item.Temperature}}</td>
        <td>{{props.item.Humidity}}</td>
        <td>{{props.item.Date}}</td>
      </template>
    </v-data-table>
  <div v-else class="spinner">
    <double-bounce/><br><br><br>
    <h4>Cargando...</h4>
  </div>
  </div>
</template>

<script>
import api from '../apiService'; 
import {DoubleBounce} from 'vue-loading-spinner'

export default {
  name: 'Data',
  components:{
    DoubleBounce
  },
  data(){
    return{
      loaded: false,
      headers:[{
        text: 'Proximity (m)',
        value: 'proximity',
        sortable: false
      },
      {
        text: 'Temperature (°C)',
        value: 'temperature',
        sortable: false
      },
      {
        text: 'Humidity (%)',
        value: 'humidity',
        sortable: false
      },
      {
        text: "Date",
        value: "date",
        sortable: false       
      }],
      data:[],
      error: ""
    }
  }, async created() {
    try{
      this.data = await api.getData();
      console.log(this.data);  
      this.loaded = true;
    }catch(err){
      this.error =  err.message; 
    }
  }
}
</script>

